/* =========================================================
   Portfolio Carousel â€“ Full Stylesheet (Fixed Height + Centered Nav)
   ========================================================= */

/* ---------- Design tokens for consistent sizing ---------- */
:root {
  --carousel-img-h: 340px;
  --carousel-content-min-h: 230px;
  --carousel-card-h: calc(var(--carousel-img-h) + var(--carousel-content-min-h));
}

/* ---------- Section ---------- */
.carousel-section {
  max-width: 1000px;
  margin: 0 auto;
  margin-bottom: 30px;
  padding: 28px 16px 40px; /* tighter top padding */
}

.section-title {
  text-align: center;
  margin: 0 0 14px; /* remove default top margin + tighter bottom */
  letter-spacing: 5px;
  color: rgb(220, 190, 255);
  text-shadow: 2px -2px 8px rgba(180, 120, 255, 0.35);
}

/* ---------- Wrapper ---------- */
.carousel-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  perspective: 1400px;
  overflow: visible;

  /* CRITICAL: make wrapper match the absolute card height */
  min-height: var(--carousel-card-h);
}

/* ---------- Track (controls absolute positioning reference) ---------- */
.carousel-track {
  position: relative;

  /* CRITICAL: give the track the real height of the cards */
  width: 100%;
  height: var(--carousel-card-h);
}

/* ---------- Cards ---------- */
.carousel-card {
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  max-width: 900px;

  border-radius: 40px;
  border: 30px;
  overflow: hidden;

  background: linear-gradient(
    to right,
    rgba(182, 221, 255, 0.55),
    rgba(247, 248, 255, 0.35)
  );

  backdrop-filter: blur(14px);
  box-shadow: 0 18px 42px rgba(0, 0, 0, 0.25);

  transform-style: preserve-3d;
  transform-origin: center center;

  transition:
    transform 0.7s cubic-bezier(0.22, 1, 0.36, 1),
    opacity 0.45s ease,
    filter 0.45s ease;
}

/* Visible border layer */
.carousel-card::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;

  pointer-events: none;
  
  /* actual visible border */
  border: 4px solid rgba(139, 122, 247, 0.35);

  /* neumorphic inner lift */
  box-shadow:
    inset 0px 4px 10px rgba(255, 255, 255, 0.6),   /* top light */
    inset 0px -4px 10px rgba(31, 38, 135, 0.381);   /* bottom shade */

}

/* ---------- Card Positions (3D horizon) ---------- */
.carousel-card.card-active {
  transform: translateX(-50%) scale(1) rotateY(0deg);
  opacity: 1;
  filter: none;
  z-index: 5;
}

.carousel-card.card-prev {
  transform: translateX(-120%) scale(0.85) rotateY(22deg);
  opacity: 0.35;
  filter: blur(1.5px);
  z-index: 3;
}

.carousel-card.card-next {
  transform: translateX(20%) scale(0.85) rotateY(-22deg);
  opacity: 0.35;
  filter: blur(1.5px);
  z-index: 3;
}

/* ---------- Image ---------- */
.carousel-image {
  height: var(--carousel-img-h);
  background: rgba(0, 0, 0, 0.03);
}

.carousel-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  filter: saturate(1.05) contrast(1.03);
}

/* ---------- Content ---------- */
.carousel-content {
  padding: 22px 26px 26px;
  font-family: "Poppins", sans-serif;

  /* CRITICAL: content height matches card height math */
  min-height: var(--carousel-content-min-h);
}

.carousel-content h3 {
  margin: 6px 0 14px;
  font-size: clamp(24px, 2.3vw, 36px);
  line-height: 1.15;
  font-weight: 650;
  color: rgba(25, 25, 25, 0.95);
}

.carousel-content .desc {
  margin: 0 0 14px;
  font-size: clamp(14px, 1.1vw, 16px);
  line-height: 1.55;
  color: rgba(30, 30, 30, 0.78);
  max-width: 80ch;
}

.carousel-content .sub {
  margin-bottom: 16px;
  font-size: 12.5px;
  letter-spacing: 0.45px;
  text-transform: uppercase;
  color: rgba(30, 30, 30, 0.55);
}

/* ---------- Link / GitHub Button ---------- */
.github-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;

  padding: 10px 16px;
  border-radius: 14px;

  border: 1px solid rgba(90, 70, 255, 0.35);
  background: linear-gradient(
    135deg,
    rgba(150, 165, 255, 0.9),
    rgba(160, 245, 225, 0.9)
  );

  color: rgba(60, 40, 210, 0.95);
  font-weight: 650;
  text-decoration: none;

  transition:
    transform 160ms ease,
    box-shadow 160ms ease,
    filter 160ms ease;
}

.github-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 14px 30px rgba(90, 70, 255, 0.28);
  filter: brightness(1.02);
}

.github-btn:active {
  transform: translateY(0) scale(0.98);
}

/* ---------- Nav Buttons (centered to actual card height) ---------- */
.nav-btn {
  position: absolute;

  /* CRITICAL: center relative to the card height, not a random wrapper height */
  top: calc(var(--carousel-card-h) / 2);
  transform: translateY(-50%);

  z-index: 10;

  width: 46px;
  height: 46px;
  border-radius: 999px;

  background: rgba(255, 255, 255, 0.9);
  border: 1px solid rgba(0, 0, 0, 0.12);

  box-shadow:
    0 14px 30px rgba(0, 0, 0, 0.18),
    0 2px 8px rgba(0, 0, 0, 0.08);

  cursor: pointer;
  user-select: none;

  transition: transform 160ms ease, box-shadow 160ms ease;
}

.nav-btn:hover {
  transform: translateY(-50%) scale(1.06);
}

.nav-btn.left { left: -8px; }
.nav-btn.right { right: -8px; }

/* ---------- Dots ---------- */
.dots {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  gap: 8px;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 999px;
  background: rgba(0, 0, 0, 0.25);
  border: 1px solid rgba(0, 0, 0, 0.15);
  cursor: pointer;
  transition: width 180ms ease, background 180ms ease, transform 180ms ease;
}

.dot:hover {
  transform: translateY(-1px);
}

.dot.active {
  width: 26px;
  background: rgba(90, 70, 255, 0.65);
  border-color: rgba(90, 70, 255, 0.35);
}

/* ---------- Mobile ---------- */
@media (max-width: 768px) {
  :root {
    --carousel-img-h: 240px;
    --carousel-content-min-h: 240px;
    --carousel-card-h: calc(var(--carousel-img-h) + var(--carousel-content-min-h));
  }

  .carousel-wrapper {
    min-height: var(--carousel-card-h);
  }

  .carousel-track {
    height: var(--carousel-card-h);
  }

  .carousel-card.card-prev,
  .carousel-card.card-next {
    display: none; /* clean mobile fallback */
  }

  .nav-btn.left { left: 6px; }
  .nav-btn.right { right: 6px; }
}

/* ---------- Reduced Motion ---------- */
@media (prefers-reduced-motion: reduce) {
  .carousel-card,
  .nav-btn,
  .github-btn,
  .dot {
    transition: none;
  }
}
